
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import { mapGetters, mapActions, mapMutations } from "vuex";
import tabBar from "@/components/tabBar.vue";
export default {
name: "inventoryManage",
props: {},
components: {
XHeader,
loading,
tabBar
},
data(){
return {
goodsData: null, // 子店铺库存列表
page: 1,
keyword: "",
userType: "sub", // 账户类型 1 2 7总店或 4 5 6 3子店铺
currentTab: 0,
plat: 3,
isLoading:true,
sub_member_id:"",

nvabarData: {
showCapsule: 1, //是否显示左上角图标
title: '库存管理', //导航栏 中间的标题
},


}
},
mounted(){
this.userType = this.$router.currentRoute.query.type;
this.getgoodslist();
},
methods: {
async getgoodslist(){
let that = this;
this.isLoading = true;
let data = {
plat: this.plat,
account: this.account,
token: this.token,
page: this.page,
kw: this.keyword,
sub_member_id: this.sub_member_id?this.sub_member_id:''
}
const [err, res] = await api.goodslist(data);
if (err) {
this.$vux.toast.text(err.msg);
this.isLoading = false
return;
}
if(res.code == "2000"){

if(!that.goodsData){
that.goodsData = res.data.list;
that.page = that.page + 1;
}else{
if(res.data.list == ''){
return this.endMore = true;
}
this.goodsData = this.goodsData.concat(res.data.list);
this.page = this.page + 1;
}
this.isLoading = false
}
},
replenishment(goods_commonid){
console.log(goods_commonid)
this.$router.push({
path: '/centerFull/myshop/replenishInfo',
query: {
goods_commonid,
sub_member_id: this.sub_member_id
}
})
}
},
computed: {
...mapGetters(["user", "account", "token"])
},
}



